.todoListMain .header input {
	padding: 10px;
	font-size: 16px;
	border: 2px solid #FFF;
	width: 165px;
}
.todoListMain .header button {
	padding: 10px;
	font-size: 16px;
	margin: 10px;
	margin-right: 0px;
	background-color: #0066FF;
	color: #FFF;
	border: 2px solid #0066FF;
}
.todoListMain .header button:hover {
	background-color: #003399;
	border: 2px solid #003399;
	cursor: pointer;
}
.todoListMain .theList {
	list-style: none;
	padding-left: 0;
	width: 250px;
}
.todoListMain .theList li {
	color: #333;
	background-color: rgba(255,255,255,.5);
	padding: 15px;
	margin-bottom: 15px;
	border-radius: 5px;
	/* list-style: none; */
	transition: background-color .2s ease-out;
}
.todoListMain .theList li:hover {
	cursor: pointer;
	background-color: pink;
}
.todoListMain .theList .deleteIcon {
	margin-top: -5px;
	margin-right: 5px;
	font-size: 24px;
	font-weight: 800;
	color:#000000;
	float: right;
	transition: color .2s ease-out;
}
.todoListMain .theList .deleteIcon:hover {
	cursor: pointer;
	color: #FF0000;
}

ul.theList {
	padding: 0;
}
